/* General Reset */
body, h1, p, ul, li, a, button {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

/* Home Container */
.home {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-links li {
  list-style: none;
}

.nav-links a {
  text-decoration: none;
  color: black;
  font-size: 1rem;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #6a11cb;
}

/* Hero Section */
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}

.hero-text {
  flex: 1;
}

.hero-text h1 {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 20px;
}

.gradient-text {
  background: linear-gradient(to right, #6a11cb, #2575fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero-text p {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #666;
}

.cta-buttons {
  display: flex;
  gap: 20px;
}

.btn {
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn.primary {
  background: #6a11cb;
  color: white;
}

.btn.primary:hover {
  background: #4a0f91;
}

.btn.secondary {
  background: white;
  color: #6a11cb;
  border: 2px solid #6a11cb;
}

.btn.secondary:hover {
  background: #6a11cb;
  color: white;
}

/* Hero Image */
.hero-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.hero-image img {
  width: 300px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  animation: fade-in 1.5s ease-out;
}

/* Animations */
@keyframes fade-in {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    text-align: center;
  }

  .hero-text h1 {
    font-size: 2rem;
  }

  .hero-text p {
    font-size: 1rem;
  }

  .hero-image {
    margin-top: 20px;
  }

  .hero-image img {
    width: 200px;
  }
}
/* Add this to your CSS file (e.g., Home.css) */

.button-container {
  display: flex;
  gap: 1rem; /* Space between buttons */
  margin-top: 20px; /* Add spacing from text */
}

.call-button, .cv-button {
  background-color: #6a11cb; /* Sky blue color */
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.call-button:hover, .cv-button:hover {
  background-color: #6a11cb; /* Darker sky blue on hover */
  transform: scale(1.05); /* Slightly enlarge on hover */
}

.call-button:active, .cv-button:active {
  transform: scale(0.95); /* Shrink slightly on click */
}
